import React from 'react'
import classes from './Search.module.css'
import {getDataHeroesSearch, getDataComicsSearch} from '../../../services/getData/getData'
import {useEffect, useState} from 'react'
import Backdrop from '../../UI/Backdrop/Backdrop'
import {Link} from 'react-router-dom'

export default function Search() {

	const input = document.querySelector('input')

	const [heroes, setHero] = useState([])
	const [comics, setComics] = useState([])
	const [inputValue, setValue] = useState('')

	useEffect(() => {
		async function searchHero() {
			if (inputValue) {
				const heroes = await getDataHeroesSearch(inputValue)
				setHero(heroes)
			} else {
				setHero('')
			}
		}
		searchHero().catch((e)=> {
			document.querySelector('main').innerHTML = e.name+ ': ' + e.message + '<br>Please, again later.'
		})

	}, [inputValue])

	useEffect(() => {
		async function searchComics() {
			if (inputValue) {
				const comics = await getDataComicsSearch(inputValue)
				setComics(comics)
			} else {
				setComics('')
			}

		}

		searchComics().catch((e)=> {
			document.querySelector('main').innerHTML = e.name+ ': ' + e.message + '<br>Please, again later.'
		})
	}, [inputValue])

	const debounce = (fn, ms) => {
		let timeout

		return function () {
			const fnCall = () => {
				fn.apply(this, arguments)
			}
			clearTimeout(timeout)
			timeout = setTimeout(fnCall, ms)
		}
	}

	function onChangeHandler() {
		setValue(input.value.trim())
	}

	const onChangeHandlerDebounce = debounce(onChangeHandler, 500)

	function closeSearch() {
		input.value = ''
		setValue('')
	}


	return (
		<>
			<div className={classes.Search}>
				<input placeholder={'Search'} type={'search'} onChange={onChangeHandlerDebounce} className={'inputSearch '+classes.Search}/>
				{inputValue ?
					<div className={classes.container}>
						{heroes.length > 0 ?
							<div className={classes.heroes}>
								<p>Heroes</p>
								<ul>
									{heroes.map((hero) => {
										return (
											<Link key={hero.id} onClick={closeSearch} to={'/heroes/' + hero.id}
												style={{textDecoration: 'none'}}>
												<li key={hero.id}>
													{hero.name}
												</li>
											</Link>
										)
									})}
								</ul>
							</div>
							: <div className={classes.heroes}><p>Hero is not found</p></div>}
						{comics.length > 0 ?
							<div  className={classes.comics}>
								<p>Comics</p>
								<ul>
									{comics.map((comics) => {
										return (
											<Link key={comics.id} onClick={closeSearch} to={'/comics/' + comics.id}
												style={{textDecoration: 'none'}}>
												<li key={comics.id}>
													{comics.title}
												</li>
											</Link>
										)
									})}
								</ul>
							</div>
							: <div className={classes.comics}><p>Comics is not found</p></div>}
					</div> : null}
			</div>

			{inputValue ?
				<Backdrop onClick={closeSearch}/> : null
			}
		</>
	)
}